* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --background: #f0efef;
}

.App {
  overflow: hidden;
}

body {
  background: #f0efef;
  background: var(--background);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: #252525;
}
/* home page */
:root {
  --yellow: #e9ff45;
}

.home-page {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
}

.home-page img {
  width: 49vw;
  height: 100vh;
  object-fit: cover;
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
}

.mid-stripe {
  height: 100vh;
  width: 0;
  border-right: 1vw solid black;
  border-left: 1vw solid #e9ff45;
  border-left: 1vw solid var(--yellow);
}

.link-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.home-page p {
  position: absolute;
  color: white;
  font-size: 60px;
}
/* nav */

:root {
  --yellow: #e9ff45;
  --background: #f0efef;
  --font: 'Lato', sans-serif;
}

.nav {
  width: 100vw;
  height: 100px;
  top: 0;
  background: #dfdfdf;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1;
  position: fixed;
  font-family: 'Lato', sans-serif;
  font-family: var(--font);
}

.nav-toggle {
  background: #f0efef;
  background: var(--background);
  width: 300px;
  text-align: center;
  height: 70px;
  margin-left: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.nav-toggle p,
.nav-toggle button{
  display: inline;
  font-size: 25px;
  border: 0;
  font-family: 'Lato', sans-serif;
  font-family: var(--font);
  font-weight: bolder;
}

.nav-toggle button {
  margin: 0 10px;
  cursor: pointer;
}

.nav-menu {
  position: relative;
  left: 15%;
  display: table;
}

.nav-menu ul {
  list-style: none;
}

.nav-menu ul li {
  display: inline;
}

.menu-link {
  text-decoration: none;
  color: black;
  border-right: 1px solid black;
  padding: 20px 50px;
  font-size: 18px;
}

.nav-menu li:nth-child(3) .menu-link {
  border-right: none;
}

.menu-link:hover {
  color: #e9ff45;
  color: var(--yellow);
}

@media only screen and (max-width: 600px) {
  .nav {
    height: 80px;
  }

  .nav-toggle {
    width: 100px;
    height: 50px;
  }

  .nav-toggle button,
  .nav-toggle p {
    font-size: 14px;
  }

  .nav-menu {
    left: 0;
  }

  .menu-link {
    padding: 10px 20px;
  }
}
/* store */

:root {
  --yellow: #e9ff45;
  --font: 'Lato', sans-serif;
}

.store {
  position: relative;
  width: 790px;
  margin: 120px auto 40px auto;
  z-index: 0;
}

.store-products {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-column: 3;
  grid-template-columns: 250px 250px 250px;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  font-family: 'Lato', sans-serif;
  font-family: var(--font);
  font-weight: bolder;
}

.store-card {
  width: 100%;
  border: 1px solid black;
}

.store-card > img {
  object-fit: cover;
  width: 100%;
  height: 280px;
}

.store-card-name,
.store-card-price{
  color: #929292;
  padding: 5px 10px;
}

.store-card-name {
  font-size: 13px;
}

.store-card-price {
  font-size: 22px;
}

.store-details-button {
  display: block;
  margin: 15px auto;
  padding: 7px 25px;
  background: #e9ff45;
  background: var(--yellow);
  border: 5px solid black;
  cursor: pointer;
  font-family: inherit;
  font-weight: inherit;
  transition: .2s linear all;
}

.store-details-button:hover {
  background: black;
  color: #e9ff45;
  color: var(--yellow);
}

.store-input-div {
  position: relative;
  width: 250px;
  height: 44px;
  line-height: 44px;
  margin: 20px 0;
  font-family: 'Lato', sans-serif;
  font-family: var(--font);
}

.store-input-div label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 10px;
  color: #d3d3d3;
  transition: 0.2s all;
  cursor: text;
  pointer-events: none;
}

.store-input-div input {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 0.5rem 10px;
  border-bottom: 2px;
  font-family: inherit;
}

input:focus~label {
  font-size: 14px;
  top: -24px;
  color: black;
}

@media only screen and (max-width: 600px) {
  .store {
    width: 100vw;
  }

  .store-products {
    grid-column: 1;
    grid-template-columns: 250px;
  }

  .store,
  .store-products {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .store-card {
    width: 60%;
  }
}
/* cart */

:root {
  --yellow: #e9ff45;
}

.cart {
  position: fixed;
  right: 0;
  top: 0;
  width: 400px;
  height: 100vh;
  z-index: 1;
  transition: -webkit-transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}

.cart-stripe {
  height: inherit;
  width: 18%;
  background: #e9ff45;
  background: var(--yellow);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 15px;
}

.cart-icon {
  cursor: pointer;
  height: 50px;
}

.cart-icon .cart-length {
  background: red;
  pointer-events: none;
  width: 20px;
  height: 20px;
  font-size: 12px;
  top: -30%;
  right: -70%;
}

.cart-content {
  width: 84%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background: black;
  padding: 20px;
  overflow-y: scroll;
}

.cart-content::-webkit-scrollbar {
  width: 10px;
}

.cart-content::-webkit-scrollbar-thumb {
  background: #e9ff45;
  background: var(--yellow);
}

.cart-button {
  width: 100%;
  margin: 0 auto;
  padding: 10px;
  cursor: pointer;
  background: black;
  color: white;
  border: 2px solid #e9ff45;
  border: 2px solid var(--yellow);
  transition: .2s linear all;
  font-size: 20px;
  font-family: 'Lato', sans-serif;
}

.cart-button:hover {
  border: 2px solid black;
  background: #e9ff45;
  background: var(--yellow);
  color: black;
}

.cart-element {
  position: relative;
  top: 0;
  color: white;
  border: 1px solid white;
  display: -webkit-flex;
  display: flex;
  margin: 20px 0;
}

.cart-element:nth-child(1) {
  margin-top: 0;
}

.cart-element > img {
  width: 90px;
  height: 150px;
  object-fit: cover;
}

.cart-element-details {
  width: 100%;
  padding-top: 10px;
}

.cart-element-details > p {
  text-align: center;
  margin-bottom: 5px;
  font-family: 'Lato', sans-serif;
}

.cart-element-details > p:nth-child(2),
.cart-element-details > p:nth-child(3) {
  font-size: 18px;
  color: #929292;
}

.cart-element-remove {
  height: 30px;
  width: 30px;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 2%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.cart-element-icon {
  color: red;
}

@media only screen and (max-width: 600px) {
  .cart {
    width: 100vw;
  }

  .cart-stripe {
    width: 16%;
  }
}
/* details */

:root {
  --background: #f0efef;
  --font: 'Lato', sans-serif;
}

.details-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(43, 43, 43, 0.8);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 4;
}

.details-content {
  background: #f0efef;
  background: var(--background);
  width: 70%;
  height: 70%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  font-family: 'Lato', sans-serif;
  font-family: var(--font);
}

.details-photo {
  height: 100%;
  width: 30%;
  object-fit: cover;
}

.details-info {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 5%;
}

.details-info > h2 {
  font-size: 30px;
}

.details-info > p {
  font-size: 22px;
  margin-top: 10px;
}

.details-sizes {
  display: -webkit-flex;
  display: flex;
}

.details-warning {
  color: red;
  margin-bottom: 20px;
}

.details-button {
  font-family: inherit;
  padding: 7px;
  font-size: 18px;
  border-radius: 5px;
}

.details-button:hover {
  background: #dfdfdf;
}

.close-icon {
  position: absolute;
  right: 0%;
  cursor: pointer;
}

.size-icon {
  cursor: pointer;
  margin: 40px 5px;
}

.size-icon:hover {
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}

@media only screen and (max-width: 600px) {
  .details-content {
    width: 90%;
    height: 80%;
  }

  .details-info {
    margin-top: 20%;
  }

  .close-icon {
    margin: 10px;
  }
}
/* podstronna info */

.info-page {
  margin-top: 100px;
  width: 100%;
  height: 500px;
}

.info-page > h1 {
  text-align: center;
  font-family: 'Lato', sans-serif;
  position: relative;
  top: 20%;
}
